<!-- 

	Video player INSPIRED BY AND IMPROVED FROM https://svelte.dev/tutorial/media-elements

 -->
<script>
	import { showControls, videoInfo } from './stores.js';

	// These values are bound to properties of the video
	let time = 0;
	let duration = 0;
	let paused = true;

	let videoInfoValue;
	let showControlsValue;
	let showControlsTimeout;

	let startedVideo = false;

	// Used to track time of last mouse down event
	let lastMouseDown;

	function handleMoveScreen(e) {
		// Make the controls visible, but fade out after
		// 2.5 seconds of inactivity
		clearTimeout(showControlsTimeout);
		showControlsTimeout = setTimeout(() => (showControls.set(false)), 2500);
		showControls.set(true);
	}

	function handleMoveBar(e) {
		// Make the controls visible, but fade out after
		// 2.5 seconds of inactivity
		clearTimeout(showControlsTimeout);
		showControlsTimeout = setTimeout(() => (showControls.set(false)), 2500);
		showControls.set(true);
		
		if (!duration) return; // video not loaded yet
		if (e.type !== "touchmove" && !(e.buttons & 1)) return; // mouse not down

		const clientX =
			e.type === "touchmove" ? e.touches[0].clientX : e.clientX;
		const { left, right } = this.getBoundingClientRect();
		time = (duration * (clientX - left)) / (right - left);
	}

	// we can't rely on the built-in click event, because it fires
	// after a drag — we have to listen for clicks ourselves
	function handleMousedown(e) {
		lastMouseDown = new Date();
	}

	function handleMouseup(e) {
		if (new Date() - lastMouseDown < 300) {
			if (paused) e.target.play();
			else e.target.pause();
		}
	}

	function handleMouseOver(e) {
		if (paused) showControls.set(true);
	}

	function loadVideo(e) {
		e.target.play();
	}

	function handleKeyDown(e) {
		let video = document.getElementById("vid");
		if (e && e.key == " ") {
			if (paused) video.play();
			else video.pause();
		}
	}

	function format(seconds) {
		if (isNaN(seconds)) return "...";

		const minutes = Math.floor(seconds / 60);
		seconds = Math.floor(seconds % 60);
		if (seconds < 10) seconds = "0" + seconds;

		return `${minutes}:${seconds}`;
	}

	showControls.subscribe(value => {
		showControlsValue = value;
	});

	videoInfo.subscribe(value => {
		videoInfoValue = value;
	});
</script>

<svelte:window on:keydown|preventDefault={handleKeyDown} />

<div id="container">
	<!-- svelte-ignore a11y-mouse-events-have-key-events -->
	<video
		id="vid"
		src="{videoInfoValue.vid}"
		on:mousedown|preventDefault|stopPropagation={handleMousedown}
		on:mouseup|preventDefault|stopPropagation={handleMouseup}
		on:mouseover={handleMouseOver}
		on:load={loadVideo}
		on:mousemove|preventDefault|stopPropagation={handleMoveScreen}
		on:touchmove|preventDefault|stopPropagation={handleMoveScreen}
		on:mousedown|preventDefault|stopPropagation={handleMoveScreen}
		on:mouseup|preventDefault|stopPropagation={handleMoveScreen}
		bind:currentTime={time}
		bind:duration
		bind:paused
	>
		<track kind="captions" />
	</video>

	<div
		class="controls"
		style="opacity: {duration && showControlsValue ? 1 : 0}"
		on:mousemove|preventDefault|stopPropagation={handleMoveBar}
		on:touchmove|preventDefault|stopPropagation={handleMoveBar}
		on:mousedown|preventDefault|stopPropagation={handleMoveBar}
		on:mouseup|preventDefault|stopPropagation={handleMoveBar}
	>
		<div class="info">
			<span class="time">{format(time)} / {format(duration)}</span>
		</div>
		<progress value={time / duration || 0} />
	</div>
</div>

<style>
	#container {
		position: fixed;
		width: 100%;
		height: 100%;
		
	}

	.controls {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		width: 100%;
		transition: opacity 1s;
		margin-bottom: 0;
		padding-bottom: 0;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		justify-items: end;
		align-content: flex-end;
	}

	.info {
		display: flex;
		width: 100%;
		justify-content: space-between;
	}

	span {
		color: white;
		text-shadow: 0 0 8px black;
		font-size: 1.4em;
		opacity: 0.7;
		margin-left: 10px;
	}

	.time {
		width: 10em;
	}

	progress {
		width: 100%;
		height: 10px;
		-webkit-appearance: none;
		appearance: none;
		margin-bottom: 5px;

		transition: all 0.3s;
	}

	progress:hover {
		height: 20px;
		margin-bottom: 0;
	}

	progress::-webkit-progress-bar {
		background-color: rgba(0, 0, 0, 0.2);
	}

	progress::-webkit-progress-value {
		background-color: rgba(255, 255, 255, 0.6);
	}

	video {
		width: 100%;
		height: 100%;
		margin: auto auto;
	}
</style>
