<script>
    import { spring } from 'svelte/motion';

    let animationFrame = 0;
    let backgroundOpacity = 100;

    let polygon = spring({ x1: screen.width*0.52, y1: 0, x2: screen.width*0.52, y2: 0, x3: screen.width*0.39, y3: 0, x4: screen.width*0.39, y4: 0 }, {
	stiffness: 0.05,
	damping: 0.64
});

    function updateAnimation() {
        animationFrame += 1;
        
        // Animation frames
        if (animationFrame == 1) {
            polygon.set({ x1: screen.width*0.52, y1: screen.height*0.35, x2: screen.width*0.52, y2: screen.height*0.35, x3: screen.width*0.39, y3: screen.height*0.23, x4: screen.width*0.39, y4: screen.height*0.46 });
        }

        else if (animationFrame == 2) {
            polygon.set({ x1: screen.width, y1: screen.height, x2: screen.width, y2: 0, x3: 0, y3: 0, x4: 0, y4: screen.height });
            backgroundOpacity = 0;
            document.documentElement.style.setProperty("--show-scrollbar", "none");
        }
    }

    // Animation states
    updateAnimation();
    setTimeout(updateAnimation, 1200);
    setTimeout(updateAnimation, 2700);
</script>

<style>
    #show {
        position: fixed;
        z-index: 200;

        top: 0;
        left: 0;
    }

    #background {
        opacity: 100%;
        transition: opacity 2s;
    }

</style>
{#if animationFrame <= 2}
<svg id="show" width="100%" height="100%" >
    <mask id="intro-mask">
        <rect width="100%" height="100%" fill="white"/>
        <polygon points="{$polygon.x1},{$polygon.y1} {$polygon.x2},{$polygon.y2} {$polygon.x3},{$polygon.y3} {$polygon.x4},{$polygon.y4}" fill="black" />
    </mask>
    <rect id="background" width="100%" height="100%" fill="#00e61b" style="opacity:{backgroundOpacity}"/>
    <rect id="hole" width="100%" height="100%" fill="black" mask="url(#intro-mask)"/>
</svg>
{/if}