<script>
    import { showInfo, videoInfo, playVideo } from './stores.js';
    import { Button, Overlay } from "svelte-materialify";
    import { fade } from 'svelte/transition';
  

    let showInfoValue;
    let videoInfoValue;

    let infoWidth = (screen.width < 880) ? 5 : 30;;

    let infoSize = {x: infoWidth, y: 5};
    let imgHeight = 40;

    let runVideo = false;

    showInfo.subscribe(value => {
		showInfoValue = value;
	});

    videoInfo.subscribe(value => {
		videoInfoValue = value;
	});

    function beginVideoPlayer() {
        runVideo = true;
        infoSize.x = 0;
        infoSize.y = 0;
        imgHeight = 100;
        document.documentElement.style.setProperty("--show-scrollbar", "hidden");

        setTimeout(runVideoPlayer, 1000);
    }

    function runVideoPlayer() {
        playVideo.set(true);

        setTimeout(resetInfo, 500);
    }

    function resetInfo() {
        runVideo = false;
        infoSize.x = infoWidth;
        infoSize.y = 5;
        imgHeight = 40;
    }

    function adaptScreen() {
        if (!runVideo) {
            infoWidth = (screen.width < 880) ? 5 : 30;
            infoSize = {x: infoWidth, y: 5};
        }
    }

    setInterval(adaptScreen, 10);
</script>

<Overlay
    style="z-index: 100; position: fixed;"
    absolute
    opacity={0.5}
    color="black"
    active={showInfoValue}
>
    <div id="show-info" style="left: {infoSize.x}%; right: {infoSize.x}%; top: {infoSize.y}%; bottom: {infoSize.y}%;">
        <div id="image" style="background-image: url({videoInfoValue.img}); height: {imgHeight}%;">
            {#if !runVideo}
                <p out:fade={{ duration: 200 }} id="title"><strong>{videoInfoValue.name}</strong></p>
                    <svg out:fade={{ duration: 200 }} viewbox="0 0 200 200" on:click="{beginVideoPlayer}">
                        <g id="play-button">
                            <circle cx="50%" cy="50%" r="40%" stroke-width="40" fill="#00e61b"/>
                            <polygon points="50 50, 150 100, 50 150" fill="white" />
                        </g>
                    </svg>
            {/if}
        </div>

        <div id="close">
            {#if infoSize.x != 0}
            <Button
                fab
                text
                size="small"
                class="white-text"
                on:click={() => {
                    showInfo.set(false);
                }}
            >
                X
            </Button>
            {/if}
        </div>

        <div id="information">
            <p id="description" style="width: 60%;">{videoInfoValue.description}</p>

            <div style="width: 40%; text-align: right;">
                <div style="margin-bottom: 10px">
                    <p><strong>Year:</strong></p>
                    <p>{videoInfoValue.year}</p>
                </div>

                <div style="margin-bottom: 10px">
                    <p><strong>Director(s):</strong></p>
                    {#each videoInfoValue.directors as directors}
                        <p>{directors}</p>
                    {/each}
                </div>

                <div style="margin-bottom: 10px">
                    <p><strong>Actors:</strong></p>
                    {#each videoInfoValue.actors as actors}
                        <p>{actors}</p>
                    {/each}
                </div>
            </div>
        </div>
    </div>
</Overlay>

<style>
    #show-info {
        position: fixed;
        z-index: 100;
        background-color: rgb(56, 56, 56);

        border-radius: 50px;
        transition: all 0.5s;
    }

    #close {
        position: absolute;
        top: 10px;
        right: 10px;
    }

    #image {
        width: 100%;
        background-size: cover;
        
        border-top-left-radius: 50px;
        border-top-right-radius: 50px;
        box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.1), 0 16px 20px 0 rgba(0, 0, 0, 0.09);

        transition: height 0.3s;
    }

    #title {
        margin-left: 30px;
        color: white;
        font-size: 20px;
        text-shadow: 0 0 5px #000000;
        width: 75%;
    }

    #information {
        display: flex;
        flex-direction: row;
        margin: 30px 30px 0;
        color: white;
    }

    p {
        font-size: 16px;
        margin-bottom: 0px;
    }

    svg {
        position: fixed;
        width: 10%;
        left: 45%;
        top: 18%;

        transition: transform 0.3s;
    }

    #play-button {
        box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.3), 0 16px 20px 0 rgba(0, 0, 0, 0.29);
        cursor: pointer;
    }

    svg:hover {
        transform: scale(1.1);
    }

    svg:focus {
        transform: scale(2);
    }

    @media screen and (max-width: 370px) {
        p {
            font-size: 12px;
        }
    }

    @media screen and (max-width: 570px) {
        p {
            font-size: 10px;
        }

        svg {
            left: 40%;
            width: 20%;
        }
    }
</style>