<link href="https://fonts.googleapis.com/css?family=Teko" rel="stylesheet" type="text/css">
<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css">

<script>
    import { Button, CardTitle, Overlay } from "svelte-materialify";
    import Player from "./Player.svelte";
    import Intro from "./Intro.svelte";
    import Info from "./Info.svelte";
    import colors from "svelte-materialify/src/utils/colors";
    import { fly } from 'svelte/transition';
    import { playVideo, showInfo, videoInfo, showControls } from './stores.js';

    let video_player_is_active = false;
    let is_fullscreen = true;

    let showControlsValue;

    let totalResultsDisplay = 20;
    let videos = [{name: 'Doctor Strange in the Multiverse of Madness', img: 'https://trailers.apple.com/trailers/marvel/doctor-strange-in-the-multiverse-of-madness/images/thumbnail_39045.jpg', description: 'Strange was a skilled neurosurgeon before nerve damage impaired his hands. Doctor Strange is described as "the mightiest magician in the cosmos" and "more powerful by far than any of your fellow humanoids" by Eternity, the sentience of the Marvel Universe.', directors: ['Sam  Raimi'], actors: ['Benedict Cumberbatch', 'Elizabeth Olsen', 'Benedict Wong'], year: '2022', vid: 'https://movietrailers.apple.com/movies/marvel/doctor-strange-in-the-multiverse-of-madness/doctor-strange-in-the-multiverse-of-madness-teaser-1_i320.m4v'},{name: 'Pirates of the Caribbean: Dead Men Tell No Tales', img: 'https://trailers.apple.com/trailers/disney/pirates-of-the-caribbean-dead-men-tell-no-tales/images/thumbnail_25749.jpg', description: "Captain Jack Sparrow finds the winds of ill-fortune blowing even more strongly when deadly ghost pirates led by his old nemesis, the terrifying Captain Salazar (Javier Bardem), escape from the Devil's Triangle, determined to kill every pirate at sea... including him.", directors: ['Joachim Rønning', 'Espen Sandberg'], actors: ['Johnny Depp', 'Orlando Bloom', 'Geoffrey Rush'], year: '2017', vid: 'https://movietrailers.apple.com/movies/disney/pirates-of-the-caribbean-dmtnt/pirates-of-the-caribbean-dmtnt-trailer-1_i320.m4v'},{name: 'Jurassic World Dominion', img: 'https://trailers.apple.com//trailers/universal/jurassic-world-dominion/images/thumbnail_39181.jpg', description: "Four years after the destruction of Isla Nublar, dinosaurs now live--and hunt--alongside humans all over the world. This fragile balance will reshape the future and determine, once and for all, whether human beings are to remain the apex predators on a planet they now share with history's most fearsome creatures in a new Era.", directors: ['Colin Trevorrow'], actors: ['Chris Pratt', 'Bryce Dallas Howard', 'Sam Neill'], year: '2022', vid: 'https://movietrailers.apple.com/movies/universal/jurassic-world-dominion/jurassic-world-dominion-trailer-1-stereo_i320.m4v'},{name: 'Lightyear', img: 'https://trailers.apple.com/trailers/disney/lightyear/images/thumbnail_38824.jpg', description: "The story of Buzz Lightyear and his adventures to infinity and beyond.", directors: ['Angus MacLane'], actors: ['Chris Evans', 'Keke Palmer', 'Dale Soules'], year: '2022', vid: 'https://movietrailers.apple.com/movies/disney/lightyear/lightyear-trailer-1_i320.m4v'},{name: 'Thor: Love and Thunder', img: 'https://trailers.apple.com/trailers/disney/thor-love-and-thunder/images/thumbnail_39419.jpg', description: "Thor enlists the help of Valkyrie, Korg and ex-girlfriend Jane Foster to fight Gorr the God Butcher, who intends to make the gods extinct.", directors: ['Taika Waititi'], actors: ['Taika Waititi', 'Natalie Portman', 'Karen Gillan'], year: '2022', vid: 'https://movietrailers.apple.com/movies/disney/thor-love-and-thunder/thor-love-and-thunder-trailer-1_i320.m4v'}];
    let videoTypes = ["Top Picks", "Action", "Drama", "Horror", "Science Fiction"];
    let videoTypesInt = 0;
    let videoTypesCall = 0;

    let videoList = [];
    
    // Makes videos into a list
    for (let i = 0; i < videoTypes.length; i++) {
        videoList.push([]);

        for (let j = 0; j < totalResultsDisplay; j++) {
            videoList[videoList.length-1].push(videos[Math.floor(Math.random() * videos.length)]);
        }
    }

    playVideo.subscribe(value => {
		video_player_is_active = value;
	});

    showControls.subscribe(value => {
		showControlsValue = value;
	});

    function getVideoTypes() {
        if (videoTypesCall == 2) {
            videoTypesCall = 0;
            videoTypesInt += 1;
        }

        videoTypesCall += 1;
        
        return videoTypes[videoTypesInt];
    }

</script>

<main>
    <Intro/>
    <Info/>
    <div id="header">
        <h1 id="title">CVideo</h1>
        <div id="categories">
            {#each videoTypes as types}
                <a href="#{types}" class="category-picks">{types}</a>
            {/each}
        </div>
    </div>
    <div class="grid">
        {#each videoList as cate}
        <div style="posiiton: relative">
            <div style="position: relative; width: 100px; height: 140px; background-color: transparent; top: 10px; z-index: 200px;" id="{getVideoTypes()}"></div>
            <p class="type-display">{getVideoTypes()}</p>
        </div>
        <div class="types">
            <div class="show-items">
            {#each cate as videoObj}    
                <div class="grid-item">
                    <div class="text-info">
                        <div style="margin: 0; min-width: 142px; min-height: 40px;">
                            <p style="font-size:10px; text-align: center; margin: 5px; text-shadow: 0px 2px #000000;"><strong>{videoObj.name}</strong></p>
                        </div>
                    </div>
            
                    <div
                        style="background-image: url({videoObj.img});"
                        class="frontpage"
                        on:click={() => {
                            let e = videoObj;
                            showInfo.set(true);
                            videoInfo.set(e);
                        }}
                    />
                </div>
            {/each}
            <div style="min-width: 20px; height: 10px"></div>
            </div>
        </div>

        {/each}
        <div style="height:50px;"/>
    </div>

    <Overlay
        style="z-index: 100"
        opacity={is_fullscreen ? 1 : 0.7}
        color="black"
        active={video_player_is_active}
        on:click={() => {
            playVideo.set(false);
        }}
    >
        <div
            id="video"
            class:fullscreen={is_fullscreen == true}
            on:click={(e) => {
                e.stopPropagation();
            }}
        >
            <div id="close">
                {#if showControlsValue}
                    <Button
                        style="z-index: 100"
                        class="error-color"
                        fab
                        text
                        size="small"
                        on:click={() => {
                            playVideo.set(false);
                            document.documentElement.style.setProperty("--show-scrollbar", "none");
                        }}
                    >
                        X
                    </Button>
                {/if}
            </div>

            
            {#if video_player_is_active}
                <Player />
            {/if}

            {#if is_fullscreen}
                <div id="gigascreen">
                    {#if showControlsValue}
                        <Button
                            style="z-index: 110"
                            size="small"
                            class="secondary-color"
                            on:click={(e) => {
                                // do not focus the fullscreenbutton if clicked
                                // this is because otherwise clicking space will cause
                                // the video player to maximize/minimize instead of pause/play
                                // when space is clicked
                                if (document.activeElement != document.body)
                                    document.activeElement.blur();

                                let div = document.getElementById("vid");
                                if (div.requestFullscreen) div.requestFullscreen();
                                else if (div.webkitRequestFullscreen)
                                    div.webkitRequestFullscreen();
                                else if (div.msRequestFullScreen)
                                    div.msRequestFullScreen();
                            }}
                        >
                            <i class="fa fa-arrows-alt" aria-hidden="true"/>
                        </Button>
                    {/if}
                </div>
            {/if}
        </div>
    </Overlay>
<src>
    
</src>

</main>

<style>
    :root {
        --netflix-color: #00e61b;
        --netflix-color-opacity: rgba(20, 229, 9, 0.3);
        --show-scrollbar: hidden;
    }

    :global(html) {
        scroll-behavior: smooth;
    }

    :global(body) {
        padding: 0;
        margin: 0;

        overflow-y: var(--show-scrollbar);
    }

    main {
        min-height: 100vh;
        width: 100vw;
        background-image: linear-gradient(180deg, rgb(52, 52, 52), rgb(41, 41, 41));
        box-sizing: border-box;
    }

    #video {
        position: fixed;
        transition: all 0.3s ease-out;
        left: 15%;
        right: 15%;
        top: 15%;
        bottom: 15%;
    }

    #video.fullscreen {
        left: 0%;
        right: 0%;
        top: 0%;
        bottom: 0%;
    }

    #video.fullscreen #gigascreen {
        position: absolute;
        bottom: 20px; /* position the top  edge of the element at the middle of the parent */
        right: 1%; /* position the left edge of the element at the middle of the parent */

        transform: translate(-50%,  0);
    }
    #video #close {
        position: absolute;
        top: -10px;
        right: -10px;
    }

    #video #fullscreen {
        position: absolute;
        top: -10px;
        left: -10px;
    }

    #video.fullscreen #close {
        position: absolute;
        top: 10px;
        right: 10px;
    }

    #video.fullscreen #fullscreen {
        position: absolute;
        top: 10px;
        left: 10px;
    }

    .grid {
        display: flex;
        justify-content: start;
        flex-direction: column;
        overflow-y: auto;
        box-sizing: border-box;
        max-width: 100%;
        min-width: 100%;
        max-height: 100%;
    }

    .types {
        display: flex;
        flex-direction: column;

        min-width: 100%;
        max-width: 100%;
        width: 100%;
        height: 220px;
        overflow-x: auto;

    }

    .show-items {
        display: flex;
        flex-direction: row;

    }

    .type-display {
        font-size: 40px;
        font-family: Teko;
        color: #fff;

        margin: 0;
        margin-top: 20px;
        margin-left: 40px;

        text-shadow: 0px 2px #000000;
    }

    .grid-item {
        margin: 40px 50px 0 50px;
        width: 142px;
        height: 80px;
        transform: scale(1.5);

        box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.2), 0 16px 20px 0 rgba(0, 0, 0, 0.19);

        background-color: #272727;
        transition: height 0.5s;
    }

    .grid-item:hover {
        height: 120px;
    }

    .frontpage {
        cursor: pointer;
        background-color: var(--netflix-color-opacity);
        font-size: 30px;
        text-align: center;
        width: 142px;
        height: 80px;
        background-size: cover; /* Resize the background image to cover the entire container */
    }

    .text-info {
        color:white;
        z-index: -1;

        position: absolute;
        bottom: 0;
    }

    #header {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        background: #222222;
        z-index: 50;
        box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.4), 0 16px 20px 0 rgba(0, 0, 0, 0.19);
    }

    #categories {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 40px;
        padding-bottom: 20px;
    }

    #title {
        color: var(--netflix-color);
        font-size: 75px;
        font-family: Teko;
        line-height: 100px;
        height: 100px;
        text-align: center;
    }

    a {
        cursor: pointer;
        color: var(--netflix-color);
        text-decoration: none;

        transition: color 0.5s;
    }

    a:hover {
        color: #fff;
    }

    /** https://onaircode.com/html-css-custom-scrollbar-examples/ */
    ::-webkit-scrollbar {
        width: 15px;
        height: 15px;
    }
    ::-webkit-scrollbar-track {
        border-radius: 10px;
        background-color: rgba(0, 0, 0, 0.306);
        margin: 0 30px;
    }
    ::-webkit-scrollbar-thumb {
        background-image: linear-gradient(45deg, #00ff15, #0ac100);
        border-radius: 10px;
        box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.2), 0 16px 20px 0 rgba(0, 0, 0, 0.19);
        -webkit-box-shadow: rgba(0, 0, 0, 0.12) 0 3px 13px 1px;
    }

    @media screen and (max-width: 510px) {
        #categories {
            gap: 10px;
        }
    }

    @media screen and (max-width: 350px) {
        .category-picks {
            font-size: 10px;
        }
    }
</style>
